@import './../../style.scss';

.page-layout {
  display: flex;

  svg {
    position: absolute;
  }
}

.page-layout__column--1 {
  background-color: $color-secondary;
  height: 100%;
  position: fixed;
  width: 25%;
  padding: 50px;
}

.page-layout__column--2 {
  margin-left: calc(25% + 100px);
  padding: 50px;
}
